import { isUrl, errorTip } from '../common/utils/validator';
import { timeStampFormat } from '../common/format/time';
import { priceFormat } from '../common/format/price';

export default {
  computed: {
    // 
    value() {
      const {
        text,
        mode,
        format,
        href,
      } = this;
      // 
      if (mode === 'price') {
        // text
        if (!/^\d+(\.\d+)?$/.test(text)) {
          errorTip('text');
        }
        // formatformat
        if (typeof format === 'function') {
          // 
          return format(text);
        }
        // format
        return priceFormat(text, 2);
      }
      if (mode === 'date') {
        // formatformat
        if (typeof format === 'function') {
          // 
          return format(text);
        }
        if (format) {
          // format
          return timeStampFormat(text, format);
        }
        // format
        return timeStampFormat(text, 'yyyy-MM-dd');
      }
      if (mode === 'phone') {
        if (typeof format === 'function') {
          // 
          return format(text);
        }
        if (format === 'encrypt') {
          //  format  encrypt
          return `${text.substr(0, 3)}****${text.substr(7)}`;
        }
        return text;
      }
      if (mode === 'name') {
        // 
        !(typeof (text) === 'string') && errorTip('text');
        if (typeof format === 'function') {
          // 
          return format(text);
        }
        if (format === 'encrypt') {
          // formatencrypt
          return this.formatName(text);
        }
        return text;
      }
      if (mode === 'link') {
        // 
        !isUrl(href) && errorTip('hrefURL');
        return text;
      }
      return text;
    },
  },
  methods: {
    // 
    formatName(name) {
      let value = '';
      if (name.length === 2) {
        value = `${name.substr(0, 1)}*`;
      } else if (name.length > 2) {
        let char = '';
        for (let i = 0, len = name.length - 2; i < len; i++) {
          char += '*';
        }
        value = name.substr(0, 1) + char + name.substr(-1, 1);
      } else {
        value = name;
      }
      return value;
    },
  },
};
